@charset "UTF-8";
/* You can add global styles to this file, and also import other style files */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

html {
  font-size: 10px;
  line-height: 1;
}
html.is-nav-open {
  overflow-y: auto;
  position: fixed;
  width: 100%;
}

body {
  color: #333;
  font-size: 1.4rem;
  line-height: 1.75;
  -webkit-text-size-adjust: 100%;
  font-family: "Hiragino Kaku Gothic Pro", "ヒラギノ角ゴ Pro W3", "メイリオ",
    Meiryo, "ＭＳ Ｐゴシック", sans-serif;
  background-color: #f2f2f2;
  position: relative;
}

a {
  text-decoration: none;
  color: inherit;
}

.m-block-app {
  width: 90%;
  margin: 40px auto 0;
  max-width: 600px;
}
.m-block-app a:nth-child(n + 2) {
  margin-top: 20px;
}
.m-btn-grad {
  position: relative;
  overflow: hidden;
  display: block;
  border-radius: 3rem;
  background-image: linear-gradient(90deg, #f5bc1f 0%, #f5bc1f 100%);
  border: 1px solid transparent;
  background-origin: border-box;
  line-height: 1.2;
  color: #fff;
  font-size: 114.28571%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  text-align: center;
}
.m-btn-grad .e-inr {
  display: block;
  padding: 18px 0;
}
